import { mapActions } from 'vuex';
import { Folder, addNewFileMenuEntry, removeNewFileMenuEntry, davParsePermissions } from '@nextcloud/files';
import { getCurrentUser } from '@nextcloud/auth';
import { NcActions, NcActionButton, NcButton, NcDialog, NcModal, NcEmptyContent, NcActionSeparator, NcLoadingIcon, isMobile } from '@nextcloud/vue';
import { UploadPicker, getUploader } from '@nextcloud/upload';
import { translate } from '@nextcloud/l10n';
import debounce from 'debounce';
import Close from 'vue-material-design-icons/Close.vue';
import Delete from 'vue-material-design-icons/Delete.vue';
// import Download from 'vue-material-design-icons/Download.vue'
// import DownloadMultiple from 'vue-material-design-icons/DownloadMultiple.vue'
import ImagePlus from 'vue-material-design-icons/ImagePlus.vue';
import MapMarker from 'vue-material-design-icons/MapMarker.vue';
import Pencil from 'vue-material-design-icons/Pencil.vue';
import Plus from 'vue-material-design-icons/Plus.vue';
import PlusSvg from '@mdi/svg/svg/plus.svg';
import ShareVariant from 'vue-material-design-icons/ShareVariant.vue';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import FetchCollectionContentMixin from '../mixins/FetchCollectionContentMixin.js';

// import ActionDownload from '../components/Actions/ActionDownload.vue'
import ActionFavorite from '../components/Actions/ActionFavorite.vue';
import AlbumForm from '../components/Albums/AlbumForm.vue';
import CollaboratorsSelectionForm from '../components/Albums/CollaboratorsSelectionForm.vue';
import CollectionContent from '../components/Collection/CollectionContent.vue';
import PhotosPicker from '../components/PhotosPicker.vue';
import HeaderNavigation from '../components/HeaderNavigation.vue';
import allowedMimes from '../services/AllowedMimes.js';
import logger from '../services/logger.js';
export default {
  name: 'AlbumContent',
  components: {
    // ActionDownload,
    ActionFavorite,
    AlbumForm,
    Close,
    CollaboratorsSelectionForm,
    CollectionContent,
    Delete,
    // Download,
    // DownloadMultiple,
    PhotosPicker,
    HeaderNavigation,
    ImagePlus,
    MapMarker,
    NcActionButton,
    NcActions,
    NcActionSeparator,
    NcButton,
    NcDialog,
    NcEmptyContent,
    NcLoadingIcon,
    NcModal,
    Pencil,
    Plus,
    ShareVariant,
    UploadPicker
  },
  mixins: [FetchCollectionContentMixin, FetchFilesMixin, isMobile],
  props: {
    albumName: {
      type: String,
      default: '/'
    }
  },
  data() {
    return {
      allowedMimes,
      showAddPhotosModal: false,
      showManageCollaboratorView: false,
      showEditAlbumForm: false,
      loadingAddCollaborators: false,
      uploader: getUploader(),
      /** @type {import('@nextcloud/files').Entry} */
      newFileMenuEntry: {
        id: 'album-add',
        displayName: t('photos', 'Add photos to this album'),
        enabled: destination => destination.basename === this.$route.params.albumName,
        /** Existing icon css class */
        iconSvgInline: PlusSvg,
        /** Function to be run after creation */
        handler: () => {
          this.showAddPhotosModal = true;
        }
      }
    };
  },
  computed: {
    /**
     * @return {import('../store/albums.js').Album|undefined} The album information for the current albumName.
     */
    album() {
      return this.$store.getters.getAlbum(this.albumName);
    },
    /**
     * @return {string[]} The list of files for the current albumName.
     */
    albumFileIds() {
      return this.$store.getters.getAlbumFiles(this.albumName);
    },
    /**
     * @return {boolean} Whether sharing is enabled.
     */
    sharingEnabled() {
      return OC.Share !== undefined;
    },
    /**
     * The upload picker context
     * We're uploading to the album folder, and the backend handle
     * the writing to the default location as well as the album update.
     * The context is also used for the NewFileMenu.
     *
     * @return {Album&{route: string, root: string}}
     */
    uploadContext() {
      return {
        ...this.album,
        route: this.$route.name,
        root: `dav/photos/${getCurrentUser()?.uid}/albums`
      };
    },
    /**
     * @return {string} The album's filename based on its name. Useful to fetch the location information and content.
     */
    albumFileName() {
      return this.$store.getters.getAlbumName(this.albumName);
    },
    albumAsFolder() {
      return new Folder({
        ...this.album,
        owner: getCurrentUser()?.uid ?? '',
        source: this.album?.source ?? '',
        permissions: davParsePermissions(this.album.permissions)
      });
    }
  },
  async mounted() {
    this.fetchAlbum();
    this.fetchAlbumContent();
    addNewFileMenuEntry(this.newFileMenuEntry);
  },
  destroyed() {
    removeNewFileMenuEntry(this.newFileMenuEntry);
  },
  methods: {
    ...mapActions(['addFilesToCollection', 'removeFilesFromCollection', 'deleteCollection', 'updateCollection']),
    async fetchAlbum() {
      await this.fetchCollection(this.albumFileName, ['<nc:location />', '<nc:dateRange />', '<nc:collaborators />']);
    },
    async fetchAlbumContent() {
      await this.fetchCollectionFiles(this.albumFileName);
    },
    redirectToNewName(_ref) {
      let {
        album
      } = _ref;
      this.showEditAlbumForm = false;
      if (this.album.basename !== album.basename) {
        this.$router.push(`/albums/${album.basename}`);
      }
    },
    async handleFilesPicked(fileIds) {
      this.showAddPhotosModal = false;
      await this.addFilesToCollection({
        collectionFileName: this.album.filename,
        fileIdsToAdd: fileIds
      });
      // Re-fetch album content to have the proper filenames.
      await this.fetchAlbumContent();
    },
    async handleRemoveFilesFromAlbum(fileIds) {
      this.$refs.collectionContent.onUncheckFiles(fileIds);
      await this.removeFilesFromCollection({
        collectionFileName: this.album.filename,
        fileIdsToRemove: fileIds
      });
    },
    async handleDeleteAlbum() {
      await this.deleteCollection({
        collectionFileName: this.album.filename
      });
      this.$router.push('/albums');
    },
    async handleSetCollaborators(collaborators) {
      try {
        this.loadingAddCollaborators = true;
        this.showManageCollaboratorView = false;
        await this.updateCollection({
          collectionFileName: this.album.filename,
          properties: {
            collaborators
          }
        });
      } catch (error) {
        logger.error('Error while setting album collaborators', {
          error
        });
      } finally {
        this.loadingAddCollaborators = false;
      }
    },
    /**
     * A new File has been uploaded, let's add it
     *
     * @param {Upload[]} uploads
     */
    onUpload: debounce(function () {
      this.fetchAlbumContent();
    }, 500),
    t: translate
  }
};